import React, { useState, useEffect } from 'react';
import axios from 'axios';

import { withStyles } from "@material-ui/core/styles";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import Tabla from './Tabla'
import SimpleMap from './MapaE'
import { Modal, Dialog, TextField, Button } from '@material-ui/core';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { makeStyles } from '@material-ui/core/styles';
import { useForm } from "react-hook-form";
import MuiDialogContent from "@material-ui/core/DialogContent";
import MuiDialogActions from "@material-ui/core/DialogActions";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Typography from "@material-ui/core/Typography";

import AccountCircle from '@material-ui/icons/AccountCircle';
import { Grid, FormControlLabel, Checkbox } from "@material-ui/core";
import { EventAvailableOutlined, EventAvailableRounded, EvStationSharp, Face } from "@material-ui/icons";
//import { alertService } from '../Component/_services';
import Slide from '@material-ui/core/Slide';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});
const styles = theme => ({
    root: {
      margin: 50,
      width: 500,
      height:500,
      padding: theme.spacing(3)
    },
    closeButton: {
      position: "absolute",
      right: theme.spacing(1),
      top: theme.spacing(2),
      color: theme.palette.grey[500]
    }
  });
  const DialogTitle = withStyles(styles)(props => {
    const { children, classes, onClose, ...other } = props;
    return (
      <MuiDialogTitle disableTypography className={classes.root} {...other}>
        <Typography variant="h6">{children}</Typography>
        {onClose ? (
          <IconButton className={classes.closeButton} onClick={onClose}>
            <CloseIcon />
          </IconButton>
        ) : null}
      </MuiDialogTitle>
    );
  });
  const DialogContent = withStyles(theme => ({
    root: {
      padding: theme.spacing(2)
    }
  }))(MuiDialogContent);
const useStyles = makeStyles((theme) => ({
    modal: {
        position: 'absolute',
        width: 400,
        backgroundColor: theme.palette.background.paper,
        border: '2px solid #000',
        boxShadow: theme.shadows[5],
        padding: theme.spacing(2, 4, 3),
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)'
    },
    iconos: {
        cursor: 'pointer'
    },
    inputMaterial: {
        width: '100%'
    }
}));

function CrudBoya() {
    const validationSchema = Yup.object().shape({

        Nombre_Boya: Yup.string()
            .required('Nombre boya Rquerido'),
        codDepert: Yup.string()
            .required('Departamento requerido'),
        codMuicipio: Yup.string()
            .required('Seleccione un municipio'),
        codRio: Yup.string()
            .required('Afluente requerido'),
        Latitud: Yup.string()
            .required('Seleccione Latitud')
            .matches(
                /^(\-?\d+(\.\d+)?).\w*(\-?\d+(\.\d+)?)$/,
                "Latitud invlida"
            ),
        Longitud: Yup.string()
            .required('Seleccione Longitud')
            .matches(
                /^(\-?\d+(\.\d+)?).\w*(\-?\d+(\.\d+)?)$/,
                "Longitud invlida"
            ),

        codCircuito: Yup.string()
            .required('Seleccione Circuito'),


    });
    const baseUrl = "https://boyaguataqui.herokuapp.com/api/boya";
    const initialValues = {
        Nombre_Boya: '',
        codDepert: '',
        codMuicipio: '',
        codRio: '',
        Latitud: '',
        Longitud: '',
        codCircuito: '',
    };

    const columns = [
        { title: 'Nombre boya', field: 'Nombre_Boya' },
        { title: 'Departamento', field: 'codDepert' },
        { title: 'Municipio', field: 'codMuicipio' },
        { title: 'Alfluente', field: 'codRio' },
        { title: 'Latitud', field: 'Latitud' },
        { title: 'longitud', field: 'Longitud' },
        { title: 'Circuito Asignado', field: 'codCircuito' },
        { title: 'Fecha', field: 'Fecha' },



    ];
    const linkForaneas = [

        "https://boyaguataqui.herokuapp.com/api/departamento/",
        "https://boyaguataqui.herokuapp.com/afluente/",
        "https://boyaguataqui.herokuapp.com//api/circuito/"
    ]
    const styles = useStyles();
    const { handleSubmit, register, errors, reset } = useForm({
        mode: "onChange"
    });
    const URL = "https://boyaguataqui.herokuapp.com/api/boya";
    const [data, setData] = useState([]);
    const [modalInsertar, setModalInsertar] = useState(false);
    const [modalEditar, setModalEditar] = useState(false);
    const [modalEliminar, setModalEliminar] = useState(false);
    const [foranea1, setForanea1] = useState([])
    const [foranea2, setForanea2] = useState([])
    const [foranea3, setForanea3] = useState([])
    const [foranea4, setForanea4] = useState([])
    const [estado, setEstado] = useState(true)
    const [titulo, setTitulo] = useState("Gestion Articulo")
    const [registro, setRegistro] = useState({})
    const [campoSeleccionado, setcampoSeleccionado] = useState({
        
    })
    const [makersLoca, setmarkersLoca] = useState({})
    const field = [
        { name: 'Tipo_Sensor', control: campoSeleccionado.Tipo_Sensor, match: /(\d{0,3})(\d{0,3})(\d{0,4})/ },
        { name: 'Voltaje', control: campoSeleccionado.Voltaje },
        { name: 'Referencia', control: campoSeleccionado.Referencia },


    ]

    function onSubmit(fields, { setStatus, setSubmitting }) {
        const campos = Object.keys(initialValues);
        if (!estado)
            peticionPost2(fields)
        else
            peticionPut2(fields)

        //   setStatus();

        // alertService.success('User added', { keepAfterRouteChange: true });


        /* if (isAddMode) {
             createUser(fields, setSubmitting);
         } else {
             updateUser(id, fields, setSubmitting);
         }*/
    }
    const foraneas = async (url, index) => {
        await axios.get(url).then(
            response => {
                switch (index) {
                    case 1:
                        setForanea1(response.data)
                        break;
                    case 2:
                        setForanea2(response.data)
                        break;
                    case 3:
                        setForanea3(response.data)
                        break;
                    case 4:
                        setForanea4(response.data)
                        break;

                }


            }
        ).catch(error => {
            console.error(error)
        })

    }
    const peticionGet = async () => {
        await axios.get(baseUrl + "/")
            .then(response => {

                setData(response.data);

                console.log("entro a consulta", data)


            }).catch(error => {
                console.log(error);
            })
    }

    const peticionPost = async () => {
        await axios.post(baseUrl + '/', campoSeleccionado)
            .then(response => {
                setData(data.concat(response.data));
                abrirCerrarModalInsertar();
            }).catch(error => {
                console.log(error);
            })
    }
    const peticionPost2 = async (fields) => {
        console.log(fields)
        await axios.post(baseUrl + '/', fields)
            .then(response => {
                setData(data.concat(response.data));
                abrirCerrarModalInsertar();
            }).catch(error => {
                console.log(error);
            })
    }
    const peticionPut2 = async (fields) => {
        await axios.put(baseUrl + "/" + fields["id"] + '/', fields)
            .then(response => {
                var dataNueva = data;
                /* dataNueva.map(campos => {
                     if (campos.id === campoSeleccionado.id) {
                         field.map((item) => {
                             campos[item.name] = campoSeleccionado[item.name];
                         })
 
                     }
                 });*/
                setData(dataNueva);
                abrirCerrarModalEditar();
            }).catch(error => {
                console.log(error);
            })
    }

    const peticionPut = async () => {
        await axios.put(baseUrl + "/" + campoSeleccionado.id + '/', campoSeleccionado)
            .then(response => {
                var dataNueva = data;
                /* dataNueva.map(campos => {
                     if (campos.id === campoSeleccionado.id) {
                         field.map((item) => {
                             campos[item.name] = campoSeleccionado[item.name];
                         })
 
                     }
                 });*/
                setData(dataNueva);
                abrirCerrarModalEditar();
            }).catch(error => {
                console.log(error);
            })
    }

    const peticionDelete = async () => {
        await axios.delete(baseUrl + "/" + campoSeleccionado.id)
            .then(response => {
                setData(data.filter(artista => artista.id !== campoSeleccionado.id));
                abrirCerrarModalEliminar();
            }).catch(error => {
                console.log(error);
            })
    }
    const agregarValores = (fields) => {
        setRegistro(fields)


    }
    const regLatLng=datos=>{
       setmarkersLoca(datos)
    }
    const seleccionarArtista = (artista, caso) => {
        const campos = Object.keys(initialValues);
        campos.map(index => {
            console.log(registro[index], " => ", artista[index], " => ", index)
            registro[index] = artista[index];
        })
        setcampoSeleccionado(artista);

        (caso === "Editar") ? abrirCerrarModalEditar()
            :
            abrirCerrarModalEliminar()
    }

    const abrirCerrarModalInsertar = () => {
        const campos = Object.keys(initialValues);
        campos.map(index => {
            registro[index] = "";
        })

        setModalInsertar(!modalInsertar);
    }


    const abrirCerrarModalEditar = () => {
        setModalEditar(!modalEditar);
    }

    const abrirCerrarModalEliminar = () => {
        setModalEliminar(!modalEliminar);
    }


    useEffect(() => {
        foraneas(linkForaneas[0], 1);
        foraneas(linkForaneas[1], 2);
        foraneas(linkForaneas[2], 3);
        peticionGet()
    }, [])


    const bodyEliminar = (
        <div className={styles.modal}>
            <p>Ests seguro que deseas eliminar al artista <b>{campoSeleccionado && campoSeleccionado.artista}</b>? </p>
            <div align="right">
                <Button color="secondary" onClick={() => peticionDelete()}>S</Button>
                <Button onClick={() => abrirCerrarModalEliminar()}>No</Button>

            </div>

        </div>
    )
    const formEditInsert = (
        <div>
        <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit}>
            {({ errors, values, touched, isSubmitting, handleChange, setFieldValue, resetForm }) => {
                return (
                    <Form>
                        <h1>{estado ? 'Creacin de boya' : 'Edicion de  boya'}</h1>
                        <div className="form-row">
                            <div className="form-group col-6">
                                <label>Nombre de la Boya</label>
                                <Field name="Nombre_Boya" type="text" className={'form-control' + (errors.Nombre_Boya && touched.Nombre_Boya ? ' is-invalid' : '')} />
                                <ErrorMessage name="Nombre_Boya" component="div" className="invalid-feedback" />
                            </div>
                        </div>
                        <div className="form-row">
                            <div className="form-group col-6">
                                <label>Departamento</label>
                                <Field name="codDepert" as="select" className={'form-control' + (errors.codDepert && touched.codDepert ? ' is-invalid' : '')}  >
                                    <option value=""></option>
                                    {foranea1.map((index, key) => (
                                        <option value={index["id"]}>{index["nombre"]}</option>
                                    ))}
                                </Field>
                                <ErrorMessage name="codDepert" component="div" className="invalid-feedback" />
                            </div>
                            <div className="form-group col-6">
                                <label>Municipio</label>
                                <Field name="codMuicipio" as="select" className={'form-control' + (errors.codMuicipio && touched.codMuicipio ? ' is-invalid' : '')}>
                                    <option value=""></option>
                                    {foranea1.map((item, key) => (
                                        (item.id === parseInt(values.codDepert) ? (
                                            item.municipios.map((index) => (
                                                <option value={index.id}>{index.nombre}</option>
                                            )
                                            )) : ('none')
                                        )))}
                                </Field>
                                <ErrorMessage name="codMuicipio" component="div" className="invalid-feedback" />
                            </div>
                            <div className="form-group col-6">
                                <label>Afluente</label>
                                <Field name="codRio" as="select" className={'form-control' + (errors.codRio && touched.codRio ? ' is-invalid' : '')}>
                                    <option value=""></option>
                                    {foranea2.map((index, key) => (
                                        <option value={index["codRio"]}>{index["nombreAfluente"]}</option>
                                    ))}
                                </Field>
                                <ErrorMessage name="codRio" component="div" className="invalid-feedback" />
                            </div>
                            <div className="form-group col-6">
                                <label>Circuito Asignado</label>
                                <Field name="codCircuito" as="select" className={'form-control' + (errors.codCircuito && touched.codCircuito ? ' is-invalid' : '')}>
                                    <option value=""></option>
                                    {foranea3.map((index, key) => (
                                        <option value={index["id"]}>{index["nombre"]}</option>
                                    ))}
                                </Field>
                                <ErrorMessage name="codCircuito" component="div" className="invalid-feedback" />
                            </div>
                        </div>
                        <div className="form-row">
                            <div className="form-group col-6">
                                <label>latitud </label>
                                <Field name="Latitud" type="text" className={'form-control' + (errors.Latitud && touched.Latitud ? ' is-invalid' : '')} value={makersLoca.lat} />
                                <ErrorMessage name="Latitud" component="div" className="invalid-feedback" />
                            </div>
                            <div className="form-group col-6">
                                <label>Longitud</label>
                                <Field name="Longitud" type="text" className={'form-control' + (errors.Longitud && touched.Longitud ? ' is-invalid' : '')} value={makersLoca.lng} />
                                <ErrorMessage name="Longitud" component="div" className="invalid-feedback" />
                            </div>
                        </div>
                        <div className="form-group">
                            <button type="submit" className="btn btn-primary">
                                Guardar
                    </button>
                            <button type="submit" onClick={() =>
                                resetForm} className="btn btn-primary">
                                Cancelar
                    </button>

                        </div>

                        { agregarValores(values)}

                    </Form>
                    
                );
                
            }}

        </Formik>
        <SimpleMap
         regLatLng={regLatLng}
        />
        </div>
    )
    const handleClose = () => {
        abrirCerrarModalInsertar()
      };
    
    return (
        <div className="App">
            {console.log(data)}
            <Tabla
                columns={columns}
                data={data}
                seleccionarArtista={seleccionarArtista}
                title={titulo}
                abrirCerrarModalInsertar={abrirCerrarModalInsertar}
                setEstado={setEstado}

            />
            <Dialog
                open={modalInsertar}
                onClose={abrirCerrarModalInsertar}
                TransitionComponent={Transition}
                keepMounted>
                {formEditInsert}
                
                <DialogTitle onClose={handleClose}>Modal</DialogTitle>
            </Dialog>



            <Modal
                open={modalEliminar}
                onClose={abrirCerrarModalEliminar}>
                {bodyEliminar}
            </Modal>
        </div>
    );
}


export  default CrudBoya 
