import React, {useState }  from 'react';
import { Navigate } from 'react-router-dom';
import DashboardLayout from './components/DashboardLayout';
import MainLayout from './components/MainLayout';
import Account from './pages/Account';
import Reporte from './pages/Crud/Reporte'


import Dialogo from './pages/dialog'
import Dashboard from './pages/Dashboard';
import Login from './pages/Login';
import NotFound from './pages/NotFound';

import CrudBoya from './pages/Crud/CrudBoya2'
import Sensor from './pages/Crud/Sensor'
import Afluente from './pages/Crud/Afluente'
import Register from './pages/Register';
import Settings from './pages/Settings';


const routes = [
  {
    path: '/',
    element: <MainLayout />,
    children: [
      { path: 'login/', element: <Login /> },
      { path: 'register', element: <Register /> },
      { path: '404', element: <NotFound /> },
      { path: '/', element: <Navigate to="/login" /> },
      { path: '*', element: <Navigate to="/404" /> }
    ]
  },
  {

    path: "app",
    element: <DashboardLayout />,
    children: [
      { path: 'account', element: <Account /> },
      { path: 'dashboard', element: <Dashboard /> },
 ,    { path: 'crudboya', element: <CrudBoya/> },
      { path: 'sensor', element: <Sensor/> },
      { path: 'afluente', element: <Afluente/> },
      { path: 'reporte', element: <Reporte/> },
    
    
      { path: 'settings', element: <Settings /> },
      { path: '*', element: <Navigate to="/404" /> }
    ]
  },
  
];


export default routes;
