function Sensor() {
    const validationSchema = Yup.object().shape({

        Tipo_Sensor: Yup.string()
            .required('Nombre boya Rquerido'),
        Voltaje: Yup.string()
            .required('Departamento requerido'),
        Referencia: Yup.string()
            .required('Seleccione un municipio'),


    });

    const baseUrl = "https://boyaguataqui.herokuapp.com/api/medicion";

    /*const columns = [
        { title: 'Tipo Sensor', field: 'Tipo_Sensor' },
        { title: 'Voltaje', field: 'Voltaje' },
        { title: 'Referencia', field: 'Referencia' }



    ];*/
    /*  const columns = [
            { title: 'Profundidad', field: 'deep' },
            { title: 'turbidez', field: 'turbidez' },
            { title: 'Temperatura',field:'temp'} ,
            { title: 'Codigo de boya', field: 'codBoya' },
            {title:'fecha',field:"fecha"},
            {title:'hora',field:"hora"}
        ]*/
    const columns = [

        { title: 'Codigo de boya', field: 'codBoya' },
        { title: 'fecha', field: "Fecha" },
        { title: 'Medicion', field: "Medicion" }
    ]

    const styles = useStyles();

    const [data, setData] = useState([]);


    const peticionGet = async () => {
        let result = [];
        await axios.get(baseUrl + "/")
            .then(response => {
                response.data.map((dat) => {
                    let obj = JSON.parse(String(dat.Medicion.replace(/ /gi, "\"")))
                    const createdAt = new Date(dat.Fecha);
                    const createdDate = createdAt.toLocaleDateString('en-US');
                    const createdTime = createdAt.toLocaleTimeString('en-US');
                    result.push({ "deep": obj.profundidad, "temp": obj.temp, "turbidez": obj.turbidez, "codBoya": dat.codBoya, "fecha": createdDate, "hora": createdTime })
                    // console.log(result) 
                    /*  fecha.push(createdDate)
                      hora.push(createdTime)*/



                })


                setData(response.data);

                console.log("entro a consulta", data)


            }).catch(error => {
                console.log(error);
            })
    }

    useEffect(() => {
        peticionGet()
    }, [])



    return (
        <div className="App">

            <Tabla
                columns={columns}
                data={data}

                title="Reporte General"

            />

        </div>
    );
}


export default Sensor
