import React, { Component ,useState,useEffect} from 'react';
import { GoogleMap, LoadScript,Circle,Marker,InfoWindow } from '@react-google-maps/api';
import {default as update} from "react-addons-update";
//AIzaSyBIfV0EMXrTDjrvD92QX5bBiyFmBbT-W8E
//AIzaSyAokKpEgS1jns2kig8jLCTBg3UYkbPcHys
import Logo from './Logo';
const containerStyle = {
  width: '550px',
  height: '400px'
};

const center = {
  lat:4.517409257093174,
  lng: -74.78970030342731 
};
const mapContainerStyle = {
  height: "400px",
  width: "800px"
}



const options = {
  strokeColor: '#FF0000',
  strokeOpacity: 0.8,
  strokeWeight: 2,
  fillColor: '#FF0000',
  fillOpacity: 0.35,
  clickable: false,
  draggable: false,
  editable: false,
  visible: true,
  radius: 30000,
  zIndex: 1
}




const SimpleMap=(props)=>{
  const [ selected, setSelected ] = useState({});
  const [ currentPosition, setCurrentPosition ] = useState({});
  const [makersLoca, setmarkersLoca] = useState({})
 const [ markers, setMarkers ] = useState([{
    position: {
      lat: 41.3954,
      lng: -74.8084106445
    
    },
    key: "Taiwan",
    defaultAnimation: 2
  }]);

 
   const [ drawMarker, setDrawMarker ] = useState(false);
   const [ id, setId ] = useState(0);
  
  const onSelect = item => {
    console.log(item,"los items")
    setSelected(item);
  }
  const success = position => {
    const currentPosition = {
    /*  lat: position.coords.latitude,
      lng: position.coords.longitude*/
      lat: 4.517409257093174,
      lng: -74.8084106445
    
    }
    setCurrentPosition(currentPosition);
  };
  useEffect(() => {
    navigator.geolocation.getCurrentPosition(success);
  })
  const onMarkerDragEnd = (e) => {
    const lat = e.latLng.lat();
    const lng = e.latLng.lng();
    setCurrentPosition({ lat, lng})
    
  };
  const addMarker = (coords) => {
  
    setId((id)=>id+1);
    setMarkers((markers) => markers.concat([{coords, id}]) )
  }
  const handleMarkerRightclick= (index, event)=> {
    
    let marca=  markers
    marca = update(markers, {
      $splice: [
        [index, 1]
      ],
    });
    setMarkers({ marca})
    
  }
  const handleMapClick = (event) => {

    let latLng = event.latLng
    setMarkers(markers.concat({ latLng, "key": Date.now(), defaultAnimation: 2 }))
    console.log(latLng.toJSON(), "longitud")
    setmarkersLoca(latLng.toJSON());
    props.regLatLng(latLng.toJSON())
   
  }
 


  return (
    <LoadScript
      googleMapsApiKey="AIzaSyBIfV0EMXrTDjrvD92QX5bBiyFmBbT-W8E"
    >
      <GoogleMap
        mapContainerStyle={containerStyle}
        zoom={10}
      
        center={center}
        onClick={handleMapClick}
       
        >
       
          
          {
            currentPosition.lat &&
            ( 
              <Marker position={currentPosition}/>
            ) 
          }
        
        {
            selected.location && 
            (
              <InfoWindow
              position={selected.location}
              clickable={true}
              onCloseClick={() => setSelected({})}
            >
              <p>{selected.name}</p>
            </InfoWindow>
            )
         }

         {
            markers.map(item => {
              return (
             
              <Marker key={item.key} 
                position={item.latLng}
                animation={item.defaultAnimation}
                onClick={() => onSelect(item)}
              />
              )
            })
         }
      
         {
            currentPosition.lat ? 
            <Marker
            position={currentPosition}
            
            onDragEnd={(e) => onMarkerDragEnd(e)}
            draggable={true} /> :
            null
          }
          {
            <Marker
            onClick={(e)=> drawMarker ? addMarker(e.latLng.toJSON()) : null}
            />
          }
          
       
     
        <></>
      </GoogleMap>
    </LoadScript>
  )
}


export default SimpleMap;